import React from 'react';
import styled from 'styled-components';

const StyledButton= styled.button`
    width:100%;
    padding:15px 0px;
    color:#fff;
    background:#999;
    border-radius:10px;
    border:none;
    &:hover{
        background:#333;
    }
`;
//{text} 값은 Button컴포넌트에 안에감싸져있기때문에 이값은 props값으로 가져오게된다 <Button textmpa=text> 이렇게 안써도된다 그런데 정확히 text 값을 가져올수있는 식은 스프레드 연산자를 사용해서 가져와야 문법이 틀리지않는다 
//스프레드연산자는 기존변수가같고있는 키워드를 그대로 {...변수} 로 카피하는것이다

const Button = ( props ) => {
    return (
        <StyledButton {...props}/>
    );
};

export default Button;