import React from 'react';
import styled from 'styled-components'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import './Homesection.scss';
import  { FaArrowCircleLeft , FaArrowCircleRight } from 'react-icons/fa'

// import imgD from '../images/img5.jpg';

const Article = styled.div`
    z-index: 1;
    position: relative;

    .slide{
        position: relative;
        height:30vw !important;
        background-size:cover;
        background-repeat:no-repeat;
        background-position:center;
        .text{
            position: absolute;
            left: 50%;
            top:50%;
            text-align: center;
            transform: translate(-50%, -50%);
            color: #fff;
            font-size: 50px;
            button{
                padding: 10px;
                border: none;
                color: #000;
                background: #fff;
                border-radius: 10px;
                &:hover{
                    background: red;
                    color: lime;
                }
            }
        }
    };

    .slide1{
        background-image:url('images/img3.jpg')
    };
    .slide2{
        background-image:url('images/img5.jpg')
    };
    .slide3{
        background-image:url('images/img7.jpg')
    };

    .slick-arrow {
        position:absolute;        ;
        top:50%;
        transform:translateY(-50%);
        background :none;       
        padding:10px;
        z-index:99999;
        width:60px;
        height:60px;
        color:#000;
            &.slick-prev{
                left:100px;
            }   
            &.slick-next{
                right:100px;
            }     
    }
    
    .slick-dots{
        position:absolute;
        bottom:70px;
        left:50%;
        transform:translate(-50%);
        li{
            &+li{
                padding-left:10px;
            };
            display:inline-block;
            button{
                width:30px;
                height:30px;
                border-radius:50%;
                background:#fff;
                text-indent:-99999px;
                overflow:hidden;
            }
            &.slick-active{
                button{
                    background:red
                }
            }
        }
    }

`;

