import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';


const NewsItemBlock = styled.div`

    display : flex;
    margin-bottom : 20px;
    width : 50%;
    max-height : 120px;
    overflow : hidden;

    .thumbnail{
        width:40%;
        padding-right:20px;
        img{
            width : 100%;
        }
    }

    .contents {
        width: 70%;
    }

`;



const NewsItem = ( {article} ) => {
    const {
        title, description, urlToImage
    } = article;
    // ( urlToImage && "yes" )  urlToImage true 값을 반환해야 출력하도록 해야한다 false 면 출력되지않도록한다
    return (
        <NewsItemBlock>

            { urlToImage &&( //urlToImage 존재할때만 보여지도록
            <div className="thumbnail">
                <Link to=
                {{
                        pathname :'/news/detail',
                        state :{
                            urlToImage,
                            title,
                            description       
                        }
                    }}
                    >
                    <img src={urlToImage} alt={title}/>
                </Link>
            </div>
            )
                }

            { description && title && ( //description나 title 존재할때만 보여지도록
            <div className="contents">
                <h2>
                    {title}
                </h2>
                <p>
                    {description.slice(0,80)}...
                </p>
            </div>
                )
            }
        </NewsItemBlock>
    );
};

export default NewsItem;