import React, { useState, useEffect } from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import { AiOutlineLoading3Quarters } from 'react-icons/ai'

const NewsSectionBlock = styled.div`
    .row {
        padding-top:80px;
        display : flex;
        flex-wrap : wrap;
    }

    .loading{
        min-height:800px;
        text-align:center;
        line-height:800px;

        .loading_start{
            font-size:100px;
            animation: loadspin  infinite 3s linear;
        }
    }

    @keyframes loadspin{
        from {
            transform : rotate(0deg);
        }to{
            transform : rotate(3turn);
        }
    }
`;


const NewsSection = ({category}) => {
    const[ 
        articles, setArticles
    ] = useState(null);
    // console.log(category)
    //로딩
    const [loading,setloading] = useState(false);
    
    const getNews = async() => {
        //클릭시 카테고리 내용변경 category 값이  all이면  '' false 면 `&category=${category}` 로변수 category를 대입한다
        //url 에 옵션을 추가할거면 &로 시작한다
        const query = category === 'all' ? '':`&category=${category}`;
        // `&category=내용` 으로담기기 때문에 공백이 있으면 안된다

        //주소창에 변수를 추가할거면 ${변수} 로 추가한다 이떄 주소는 `` 로묶어여한다
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=b1b3453b21e6430f88140fc3ea2802b1`);
        console.log(response)
        //로딩화면 데이터가 도착하기전엔 변수 loading에 트루값변환 
        // setloading(true)
        //로딩화면 데이터가 도착하기전엔 변수 loading에 거짓값변환 
        setloading(true);
        setArticles( response.data.articles ); 
        // console.log(setloading)
    };

    useEffect(
        () => {
            getNews()
        },[category]
        //useEffect 뒤에 ,[ 변수 ] 를넣는것은 useState 값이 변할때마다 수행되는 값을 넣을수있다 클래스컴포넌트의 componentUpdate 와 비슷하다
    );


    if( !loading ){
        return <NewsSectionBlock>
           <div className="loading"> <AiOutlineLoading3Quarters className="loading_start"/></div>
        </NewsSectionBlock>
    }else{  //로딩끝 (데이터를 다불러옴)
        if( !articles ) {    //초기값이 값이 없다면 null 값을 리턴  이작업이 없으면 화면에서 type errow map() 출력된다
            return <NewsSectionBlock></NewsSectionBlock>;
        }else{
            return (
                <NewsSectionBlock>
                   
                    <div>
                        <div className="row ">
                            {
                                articles.map( ( article ) => (
                                    <NewsItem  key={article.url} article={article} /> 
                                    ) )
                            }
                        </div>
                    </div>
                </NewsSectionBlock>
            );
        }
    }

   

    
};

export default NewsSection;