import React from 'react';
import styled,{ css } from 'styled-components'  //css33 번줄 라이브러리

const CategoryBlock = styled.div`
    border-top:80px solid #999;
    .row{
        display:flex;
        padding-top:50px;
        justify-content:center;
    }
`;
//&+&  & 자기자신 +& 자기다음동생
const Cate = styled.div`
    cursor: pointer;
    color: black;
    padding : 0 20px;
    margin : 0 20px;
    ${  //${ 매개변수 => 매개변수.속성값 &&  css  }
        props => props.active && css`
        color : #22b8cf;
        border-bottom : 2px solid #22b8cf;
        font-weight : bold;
        ` //Cate.map( active = true ) 일때 스타일변경
    }
`;
const categories = [    // key값의 name 변수들은  api데이터와 맞춰줘야하고 화면에 출력할 text 는 사용자 임의지정이다
    {name : 'all',
    text:'전체보기'
        },
    {name : 'business',
    text:'비지니스'
        },
    {name : 'entertainment',
    text:'엔터테인먼트'
        },
    {name : 'health',
    text:'건강'
        },
    {name : 'science',
    text:'과학'
        },
    {name : 'sport',
    text:'스포츠'
        },
    {name : 'technology',
    text:'기술'
        }
 ]




const Category = ( { category , onSelect } ) => {    //클릭이벤트 발생 
    //news.js 에서 배열 {category} 를 전달해준다 또한 props값으로 {onSelect} 클릭이벤트 함수로 date.text를 클릭시 data.name 의값을  true로 만들어준다
    return (
        <CategoryBlock>
            <div className="row">
                { 
                    categories.map(
                        ( data ) => ( 
                            <Cate key={data.name} active={category === data.name } onClick={()=> onSelect(data.name)} > {data.text} </Cate>
                        )
                    )
                 }
            </div>
        </CategoryBlock>
    );
};

export default Category;