import React, {Component, useEffect } from 'react';
import Header from '../component/Header';
import Footer from '../component/Footer';
import styled from 'styled-components';

const NewsDetailStyle = styled.div`
border-top : 80px solid #999;

.Detail{
    padding: 40px;
    margin: 100px 0;
    .Detail_align{
        display: grid;
        grid-template-rows: auto;
        .detail_img{
            min-height:300px;
            padding-bottom: 40px;
            text-align:center;
            img{
                width: 50%;
            }
        }
        .detail_about{
            margin-left: 100px;
            text-align: center;
            padding-top: 150px;
            h3{
                font-size: 40px;
                margin-bottom: 20px;
            }
            p{
                font-size: 15px;
                margin-bottom: 30px;
            }
        }
    }
}

`;

const NewsDetailBlock = styled.div`
    border-top : 80px solid #999;
    
    .row{
        margin-top:50px;
        .detail_img{
            padding-bottom: 40px;
            text-align:center;
            img{
                width: 50%;
            }
        }
            p:nth-child(1){
                font-size: 40px;
                margin-bottom: 20px;
            }
            p:nth-child(2){
                img{
                    width:50%
                }
            }
            p{
                text-align:center;
                font-size: 15px;
                margin-bottom: 30px;
            }
    }

`

