const NewsDetail = (props) => {

    
    const {location,history} = props
    useEffect(
        () => {
            if( location.state === undefined ){
                history.push('/')
            }
        },[]   // []은 특정값에 의존하지않고 무조건 한번 수행하겠다는 의미이다 빈배열일때는 한번만 수행한다
    )

    if( location.state === undefined ){
        return null
    }else{
        return (
            <div className="wrap">
                <Header/>
                <NewsDetailBlock>
                    <div className="row">
                        <p>{location.state.title}</p>
                        <p>
                            <img src={location.state.urlToImage} alt={location.state.title}/>
                        </p>
                        <p>
                            {location.state.description}
                        </p>
                    </div>
                </NewsDetailBlock>
                <Footer/>
            </div>
        );
    }
   
};

export default NewsDetail;