import React from 'react';
import styled,{css} from 'styled-components';
import First from './First';
import Second from './Second';
import Third from './Third';

const MenuListBlock = styled.div`
    .tabs{
        display:flex;
        justify-content:center;
    }
`;

const List = styled.div`
    margin:0 50px;
    padding:10px 20px;
    background:#ddd;
    border-radius:10px;
    cursor:pointer;
    ${
        props => props.activeTab && css`
            background:#333;
            color:#fff;        
        `
    }
`;

const obj = [
    {id:0,title:"탭매뉴1",tabCont:<First /> }, //active=usestate(0) 과같은값이넘어온다
    {id:1,title:"탭매뉴2",tabCont: <Second />},
    {id:2,title:"탭매뉴3",tabCont:<Third />},
]

const MenuList = ( {activeTab,onSelect} ) => {
    return (
        <MenuListBlock>
            <div className="row">
                <div className="tabs">
                    {
                        obj.map( (data)=>(
                            <List activeTab = { activeTab === data.id } onClick={()=>{
                                onSelect(data.id)
                            }}  key={data.id} >
                                {data.title}
                            </List>
                        ) )
                    }
                </div>
                <div className="constents">
                    {
                        obj[activeTab].tabCont
                    }
                </div>                
            </div>
        </MenuListBlock>
    );
};

export default MenuList;